.page {
  background-color: #000;
  font-family: "Inter", Arial, sans-serif;
  margin: 0 auto;
  max-width: 100%;
  min-width: 320px;
  min-height: 100vh;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  color: white;
}
